@import url(https://fonts.googleapis.com/css2?family=Baloo+Chettan+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
}

.App {
    text-align: center;
    font-size: 18px;
    font-family: 'Baloo Chettan 2', cursive;
	min-width: 375px;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
header {
    background-color: white;
}

header .header-row {
	display: block;
	text-align: left;
	padding: 10px 30px;
}

header .header-row h1 {
	font-size: 1.5em;
	margin: .2em 0;
}

header .header-row .disclaimer {
	padding-left: 0px;
    font-size: 1.2em;
	display: flex;
	align-items: center;
}

header p {
	margin: 0;
}

header .header-logo,
header .header-logo img {
	height: 40px;
	margin: 0;
}

@media only screen and (min-width: 600px) {
	header .header-row {
		padding: 20px 50px;
	}

	header .header-row h1 {
		font-size: 1.8em;
		/* padding: 0 0 0 30px; */
		margin: 0 0 .2em 0;
	}

	header .header-row .disclaimer {		
		/* padding-left: 30px; */
		font-size: 1.4em;
	}

	header .header-logo,
	header .header-logo img {
		height: 45px;
	}
}

@media only screen and (min-width: 1024px) {
	header .header-row {
		display: flex;
		justify-content: space-between;
		padding: 20px 60px;
	}

	header .header-row h1 {
		font-size: 1.9em;
		margin: 0;
	}

	header .header-row .disclaimer {
		font-size: 1.5em;
	}

	header .header-logo,
	header .header-logo img {
		height: 50px;
	}
}
nav {
    /* height: 50px; */
    background-color: rgb(255, 141, 80);
}

nav .nav-row {
	height: 100%;
    padding: 0 10px;
	display: flex;
	flex-direction: row;
	justify-content: start;
	align-items: center;
}

nav .nav-button {
	font-size: 1.2em;
	text-decoration: none;
	color: white;
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 10px 20px;
}

@media only screen and (min-width: 600px) {
	nav .nav-row {
		padding: 0px 30px;
	}

	nav .nav-button {
		font-size: 1.4em;
		padding: 12px 20px;
	}
}

@media only screen and (min-width: 1024px) {
	nav .nav-row {
		padding: 0px 40px;
	}
}
.gallery {
	max-width: 960px;
	background-color: rgb(82, 82, 82);
	border: 4px solid darkgray;
	border-width: 0px 1px;
	margin: 0 auto;
}

.gallery .gallery-title-area {
	padding: 20px;
	color: white;
}

.gallery .gallery-preview-title {
	font-family: 'Playfair Display', serif;
	font-weight: normal;
	font-size: 1.5em;
	letter-spacing: .02em;
	margin: unset;
}

.gallery .gallery-preview {
    /* border: 1px solid black; */
	padding: 20px;
	border-top: 3px solid lightgray;
	border-bottom: 3px solid lightgray;
}

.gallery .gallery-preview-img {
    width: 800px;
	max-width: 100%;
    /* border: 4px solid black; */
}

.gallery .gallery-thumbnails {
	padding: 20px;
	display: grid;
	grid-gap: 10px;
	gap: 10px;
	grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
	grid-auto-rows: auto;
}

.gallery .gallery-thumbnails img {
	width: 100%;
}

@media only screen and (min-width: 600px) {
	.gallery .gallery-preview-title {
		font-size: 1.8em;
	}

	.gallery .gallery-thumbnails {
		grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
	}
}

@media only screen and (min-width: 840px) {
	.gallery .gallery-thumbnails {
		padding: 20px 40px;
	}
}
footer {
	color: white;
	font-size: 1.1em;
	background-color: rgb(101, 147, 182);
	padding: 10px 20px;
	/* text-align: left; */
}

footer p {
	margin: unset;
}

footer .my-info {
	margin: .5em 0;
}

footer a {
	color: unset;
}

footer a.twitter::before {
	display: inline-block;
	content: "";
	background-image: url(data:image/png;base64,);
	background-size: 1.3em 1.3em;
	height: 1.3em;
	width: 1.3em;
	background-repeat: no-repeat;
	vertical-align: -.4em;
}

footer .credits {
	font-size: 0.9em;
	margin: .3em 0;
}

@media only screen and (min-width: 600px) {
	footer {
		font-size: 1.3em;
	}
}
